import React, { Component } from 'react';
import { graphql, compose } from 'react-apollo';
import AdminView from '../components/AdminView';
import Loader from '../../common/components/global/components/Loader';

import ProductSpecs from '../components/ProductSpecs';
import { ADMIN_PRODUCTS_QUERY, MODIFY_PRODUCT_DATA } from '../../common/schema';

const columns = [
  {
    title: (
      <a href="#">
        {/* {t('users.column.name')} {this.renderOrderByArrow('username')} */}
        Name
      </a>
    ),
    dataIndex: 'node',
    key: 'handle',
    // eslint-disable-next-line
    render: (text, record) => (
      <div>
        {/* <Link to={{ pathname: `/admin/locations/${record.node.handle}` }}>{text}</Link> */}
      </div>
    )
  }
];
class Products extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    const { productsLoading, sfy_shop } = this.props;
    console.log('The PRODUCT PROPS::::', sfy_shop);

    if (productsLoading) {
      return (
        <AdminView {...this.props}>
          <Loader />
        </AdminView>
      );
    }
    return (
      <AdminView {...this.props}>
        <ProductSpecs dataSource={sfy_shop.products} columns={columns} />
      </AdminView>
    );
  }
}
const ProductsApollo = compose(ADMIN_PRODUCTS_QUERY())(Products);

export default ProductsApollo;
